import React from "react";
import { Box, Typography, alpha, useTheme, Tooltip } from "@mui/material";
import { PersonAdd } from "@mui/icons-material";
import { CharacterNeedsBar } from "./CharacterNeedsBar";
import { ActionIndicator } from "./ActionIndicator";

export interface PartyMemberCardProps {
  portrait?: string; // Portrait image path (if character exists)
  name?: string; // Character name
  title?: string; // Character title (epithet + role)
  level?: number; // Character level
  isPlayer?: boolean; // Is this the player's character?
  isSelected?: boolean; // Is this card currently selected?
  isEmpty?: boolean; // Is this an empty slot?
  needs?: {
    mood: number;
    energy: number;
    satiety: number;
  };
  nextAction?: string; // Name of next action to execute
  actionType?: string; // Type of action
  onClick: () => void;
}

/**
 * Reusable party member portrait card
 * Shows character portrait, name, level
 * Supports empty slot state
 */
export const PartyMemberCard: React.FC<PartyMemberCardProps> = ({
  portrait,
  name,
  title,
  level,
  isPlayer = false,
  isSelected = false,
  isEmpty = false,
  needs,
  nextAction,
  actionType,
  onClick,
}) => {
  const theme = useTheme();

  // Determine glow color - only player gets special color, others are normal
  const glowColor = isPlayer
    ? theme.palette.primary.main // Purple for player
    : theme.palette.text.disabled; // Grey for normal (no selected state)

  return (
    <Box
      onClick={onClick}
      sx={{
        position: "relative",
        width: 80,
        cursor: "pointer",
        transition: "all 0.25s ease-out",
        
        "&:hover": {
          transform: "translateY(-2px) scale(1.05)",
        },

        "&:active": {
          transform: "translateY(-1px) scale(1.02)",
        },
      }}
    >
      {/* Portrait Circle */}
      <Box
        sx={{
          width: 80,
          height: 80,
          borderRadius: "50%",
          border: isEmpty
            ? `3px dashed ${theme.palette.text.disabled}`
            : `3px solid ${glowColor}`,
          backgroundColor: isEmpty
            ? alpha("#fff", 0.3)
            : theme.palette.background.paper,
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          overflow: "hidden",
          position: "relative",
          
          // Glow effect - only player gets strong glow
          boxShadow: isEmpty
            ? "none"
            : isPlayer
            ? `
              0 0 24px ${alpha(glowColor, 0.6)},
              inset 0 2px 4px ${alpha("#000", 0.1)}
            `
            : `
              0 0 8px ${alpha(glowColor, 0.2)},
              inset 0 2px 4px ${alpha("#000", 0.1)}
            `,

          // Inner highlight
          "&::before": {
            content: '""',
            position: "absolute",
            top: 0,
            left: 0,
            right: 0,
            height: "40%",
            background: `linear-gradient(180deg, ${alpha("#fff", 0.3)} 0%, transparent 100%)`,
            borderRadius: "50%",
            pointerEvents: "none",
          },
        }}
      >
        {isEmpty ? (
          // Empty slot - show add icon
          <PersonAdd
            sx={{
              fontSize: "3rem",
              color: theme.palette.text.disabled,
              opacity: 0.5,
            }}
          />
        ) : portrait ? (
          // Character portrait image
          <Box
            sx={{
              width: "100%",
              height: "100%",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              overflow: "hidden",
            }}
          >
            <img
              src={portrait?.startsWith("/") ? portrait : `/img/portraits/${portrait}.png`}
              alt={name || "Character"}
              style={{
                width: "100%",
                height: "100%",
                objectFit: "cover",
              }}
            />
          </Box>
        ) : (
          // Fallback - show initials or placeholder
          <Typography
            sx={{
              fontFamily: "Cinzel, serif",
              fontSize: "1.75rem",
              fontWeight: 700,
              color: theme.palette.text.secondary,
            }}
          >
            {name?.charAt(0) || "?"}
          </Typography>
        )}
      </Box>

      {/* Character Name and Title - 2 lines */}
      {!isEmpty && name && (
        <Typography
          sx={{
            textAlign: "center",
            fontFamily: "Crimson Text, serif",
            fontSize: "0.8rem",
            fontWeight: 500,
            color: isPlayer ? theme.palette.primary.main : theme.palette.text.primary,
            mt: 1.5,
            lineHeight: 1.3,
            textShadow: isPlayer
              ? `0 0 6px ${alpha(theme.palette.primary.main, 0.4)}`
              : "none",
            whiteSpace: "pre-line", // Allow line breaks
          }}
        >
          {name}
          {title && `\n${title}`}
        </Typography>
      )}

      {/* Empty slot text */}
      {isEmpty && (
        <Typography
          sx={{
            textAlign: "center",
            fontFamily: "Crimson Text, serif",
            fontSize: "0.9rem",
            fontWeight: 500,
            color: theme.palette.text.disabled,
            mt: 2,
            fontStyle: "italic",
          }}
        >
          Recruit
        </Typography>
      )}
    </Box>
  );
};

